html,body{height:100%;margin:0;padding:0;transition:background-color 4s;width:100%;background-color:#333}@media(prefers-color-scheme: light){html,body{background-color:#ccc}}.coox-hero{display:flex;flex-direction:column;height:100%;justify-content:center;margin:0;padding:0;position:absolute;text-align:center;width:100%}.coox-hero .coox-hero-title{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:20vw;line-height:30vw;margin:0;padding-bottom:2vw;transition:color 4s,font-size 1s;z-index:1;color:#ccc}@media(prefers-color-scheme: light){.coox-hero .coox-hero-title{color:#333}}.coox-wave-strip-container{display:flex;flex-direction:column;height:100%;justify-content:center;position:absolute;width:100%}.coox-wave-strip-container .coox-wave-strip{height:100%;transition:border-color 4s,height 1s;width:100%;border-bottom:1vw solid #999;border-top:1vw solid #999}@media(prefers-color-scheme: light){.coox-wave-strip-container .coox-wave-strip{border-bottom:1vw solid #777;border-top:1vw solid #777}}.coox-wave-strip-container .coox-wave-strip-color-computer{height:0;line-height:0;margin:0;padding:0;transition:background-color 4s,color 1s;width:0;background-color:#111;color:#666}@media(prefers-color-scheme: light){.coox-wave-strip-container .coox-wave-strip-color-computer{background-color:#eee;color:#aaa}}